@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&family=Bricolage+Grotesque:opsz@10..48&family=Inconsolata&family=Noto+Sans:wght@600&family=Open+Sans&family=Roboto:wght@500&display=swap);
/* @import ".//App.css"; */
nav {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    background-color: var(--color-primary);
    top: 0;
    z-index: 10;
}
nav h1 {
    text-transform: uppercase;
}
nav main {
    width: 70%;
    display: flex;
    justify-content: flex-end;
}
nav main a{
    color: var(--color-fourth);
    margin: 1rem;  
}
nav main a:hover {
    color: var(--color-third);
}
/* Import font  */
/* CSS reset  */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    scroll-behavior: smooth;
}
/* Properties  */
:root {
     --font-family-primary: 'Roboto', sans-serif;
     --font-family-secondary: 'Bricolage Grotesque', sans-serif;;
     --color-primary: white;
     --color-secondary: #090c31;
     --color-third: #5853ff;
     --color-fourth: rgb(44,44,44);
}


.home {
    width: 100%;
    height: 100vh;
    background-color: var(--color-secondary);
    
}
.home::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: url("https://img.freepik.com/free-vector/abstract-background-with-flowing-particles-design_1048-13879.jpg?w=740&t=st=1692107720~exp=1692108320~hmac=6f7b24ca0c38b5024ae2114e002a6214feadb4199d6cacab14b66b879f06cbd2")no-repeat center center/cover;
    -webkit-filter: blur(3px);
            filter: blur(3px);
    border-radius: 0 0 200px 200px;
}
.home main {
    position: absolute;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    height: 100%;
    padding: 2rem;
    justify-content: center;
}
.home main h1 {
    font: 400 4rem var(--font-family-primary);
    color: var(--color-primary);
}
.home main p {
    font: 400 1rem var(--font-family-secondary);
    color: var(--color-primary);
}
.home2 {
    width: 100%;
    height: 100vh;
    background-color: var(--color-secondary);
    padding: 500px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-top: -200px; */
}
.home2 img {
    width: 25%;
    -webkit-animation: imgUp 0.7s linear infinite alternate;
            animation: imgUp 0.7s linear infinite alternate;
}

@-webkit-keyframes imgUp {
    to{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    
}

@keyframes imgUp {
    to{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    
}

.home2 div {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem;
    height: 100vh;
}
.home2 div p {
    color: var(--color-primary);
    letter-spacing: 2px;
    word-spacing: 5px;
    font: 100 1.2rem var(--font-family-secondary);
}
.home3{
    width: 100%;
    height: 120vh;
    background-color: var(--color-secondary);
    margin-top: -200px;
    display: flex;
}
.home3  div {
    width: 70%;
    height: 100vh;
    background-color: var(--color-primary);
    border-radius: 0 200px 200px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}
.home3  div h1 {
    text-transform: uppercase;
    border-bottom: 2px solid var(--color-fourth);
    padding: .5rem;
    width: 15rem;
    color: var(--color-fourth);
}
.home3  div p {
    padding: 5rem;
    letter-spacing: 1px;
    word-spacing: 5px;
    line-height: 170%;
    font: italic 100 1.2rem var(--font-family-secondary);
}
.home4 {
    width: 100%;
    height: 100vh;
    background-color: var(--color-secondary);
    display: flex;
    justify-content: flex-end;
}
.home4 > div {
    background-color: var(--color-third);
    border-radius: 200px 0 0 200px;
    width: 70%;
    height: 70vh;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home4 h1 {
    color: var(--color-primary);
    text-transform: uppercase;
    width: 140px;
    border-bottom: 2px solid var(--color-primary);
    padding: 0.5rem;
}
.home4 article {
display: flex;
margin: 2rem 0;
flex-wrap: wrap;
justify-content: center;
}
.home4 article > div {
    margin: 1rem;
    height: 10rem;
    width: 10rem;
    background-color: var(--color-primary);
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--color-fourth);
    cursor: pointer;
    transition: all 0.5s;
    -webkit-animation: imgUp 0.7s linear infinite alternate;
            animation: imgUp 0.7s linear infinite alternate;
}
.home4 article > div:hover {
    -webkit-filter: invert(1);
            filter: invert(1);
}
.home4 article svg {
    font-size: 4rem;
}
.home4 article p {
    font: 900 1rem var(--font-family-secondary);
}
.contact {
    width: 100%;
    height: 100vh;
    background-color: var(--color-secondary);
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact main {
    background-color: var(--color-primary);
    height: 40rem;
    width: 70%;
    padding: 3rem;
    border-radius: 0 200px 200px 0;
}
.contact main form {
    margin: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact main form div {
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 60%;
    margin: 0.5rem;
}
 .contact label  {
        font: 400 2rem var(--font-family-primary);
    width: 35%;
    margin: 0 1rem;
}
 .contact input {
    align-self: stretch;
    width: 80%;
    padding: 1rem;
    border-radius: 5px;
    border: 1px solid rgba(123,123,123,0.6);
    outline: none;
}
.contact button {
margin: 3rem 0;
padding: 0.7rem 4rem;
border: none;
border-radius: 5px;
color: var(--color-third);
font: 500 1rem var(--font-family-primary);
}
.contact button:hover {
    opacity: 0.9;
}
footer{
    background-color: var(--color-primary);
    height: 200px;
    width: 100%;
    padding: 2rem;
    color: var(--color-fourth);
    display: grid;
    grid-template-columns: 4fr 1fr;
}
footer div {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
footer div h1 {
    font: 700 1rem var(--font-family-primary);
    text-transform: uppercase;
    color: var(--color-third);
}
footer div p {
    font: 500 1.2rem var(--font-family-primary);
}
footer h5 {
    font: 300 2rem var(--font-family-primary);
    margin: 1rem 0; 
    text-align: center;
}
footer h5 > div {
display: flex;
flex-direction: column;
justify-content: center;
}
.center {
    align-items: center;
}
 a {
    color: var(--color-fourth);
    font: 900 1rem var(--font-family-secondary);

}
 a:hover {
    color: var(--color-third);
}
@media screen and (max-width:820px){
.home3 {
    height: 162vh;  
  }  
  .home3 div {
    height: 120vh;
  }
  .contact main {
    height: 100vh;
    width: 100%;
    border-radius: 0;
  }
  .contact main h1 {
    text-align: center;
  }
  .contact main form div {
    width: 117%;
}
}
@media screen and (max-width:700px){
    .home::before {
        border-radius:0;
    }
nav h1 {
display: none;
}
nav main {
    width: 100%;
    justify-content: center;
}
nav main a {
    margin: 0.3rem;
}
.home main {
    align-items: center;
}
.home main h1 {
   font-size: 3rem;
}
.home2 {
    height: 200vh;
    flex-direction: column;
}
.home2 img {
    width: 100%;
}
.home2 div {
    width: 100%;
}
.home3 {
    height: 146vh;
}
.home3 div {
    text-align: center;
    width: 100%;
    height: 147vh;
    border-radius: 0;
}
.home3  div p {
    padding: 5rem 2rem;
    font-size: .9rem;
    line-height: 150%;
}
.home4 {
    align-items: center;
    height: 153vh;
}
.home4 > div  {
    border-radius: 0;
    width: 100%;
    height: unset;
}
.contact main {
padding: 1rem;
}
.contact main > h1 {
    margin: 1rem;
}
.contact main form {
    margin: 0;
}
.contact main form div {
    width: 100%;
}
.contact label  {
  font-size: 1rem;
}
footer {
      grid-template-columns: 1fr;
}
footer div  {
    align-items: center;
    margin-bottom: 20px;
}
    }
